.c-btn {
  padding: 10px 14px;
  background-color: #e3e3e4;
  cursor: pointer;
  border-radius: 8px;
  border: 1px solid #e3e3e4;
  position: relative;
  font-size: 13px;
  @media ( max-width: 768px ) {
    padding: 8px;
    font-size: 11px; }

  &__background {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    z-index: 0; }
  &__content {
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2; }
  &--two-columns {
    .c-btn__content {
      display: grid;
      grid-gap: 4px;
      grid-template-columns: repeat(2, auto); } }
  &--positive {
    background-color: #bee2aa; }
  &--negative {
    background-color: #e2aaaa; }
  &--neutral {
    background-color: #afd4ff; }
  &--unclassified {
    background-color: #ffe7ce; }
  &--active {
    border-color: #0d2e57; }
  &--small {
    padding: 4px 8px;
    font-size: 11px; }
  &--outline {
    border: none; }
  &--block {
    width: 100%; }
  &:hover {
    & > .c-btn__background {
      background: rgba(0,0,0,.2);
      display: inline; } }
  &:active, &:focus {
    box-shadow: 0px 0px 0px 1px black;
    outline: 0; } }
